.page {
  &__body {
    margin: 0;
  }

  &__menu {
    position: fixed;
    left: 0;
    top: 0;
    right: 0;

    transform: translateX(-100%);
    transition: transform 0.3s;

    &:target {
      transform: translateX(0);
    }
  }

  &__section {
    margin-bottom: 120px;

    &-title {
      margin: 0 0 48px;
      font-weight: 700;
      font-size: 32px;
      line-height: 32px;

      text-align: center;
      letter-spacing: -1.5px;
    }

    &:first-of-type {
      padding-top: 120px;
    }

    @include onTablet {
      margin-bottom: 64px;
      font-size: 40px;
      line-height: 100%;
    }

    @include onDesctop {
      margin-bottom: 64px;
      font-size: 48px;
      line-height: 48px;
    }
  }
}
